function Social({ id, link, icon, text, iconStyle }) {
  return (
    <Col md={12} sm={12} xs={3}>
      <a href={link} id={id} target="_blank" rel="noopener noreferrer">
        <Row className="profile-social">
          <Col md={3} sm={3} xs={12}><FontAwesomeIcon icon={icon} className="fa-lg" style={iconStyle} /></Col>
          <Col className="social d-none d-md-block" md={9}>{text}</Col>
        </Row>
      </a>
    </Col>)
}


