import React, { useState } from 'react';
import { Row, Col } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExpand, faMinus } from '@fortawesome/free-solid-svg-icons';

export function Research({ title, authors, venue, options, children }) {

    const hasContent = children ? 1 : 0;
    const [open, setOpen] = useState(0);

    const details = (<div>
        <span className="research-authors">{authors}</span><br />
        <span className="research-title"> {title}</span><br />
        <span className="research-others"> {venue}</span>
    </div>);

    const content = (<div>
        {details}
        <div style={{ position: 'absolute', top: '8px', right: '0px' }}>
            {options}
            {hasContent ? open ? <FontAwesomeIcon icon={faMinus} /> : <FontAwesomeIcon icon={faExpand} /> : null}
        </div>
        {open ? children : null}
    </div>);

    const classes = hasContent ? 'expandable' : '';

    return (<Col md={12} className="research-container">
        <Row className={`research ${classes}`} onClick={() => setOpen(!!!open)}>
            {content}
        </Row>
    </Col >)
}