import React, { ReactChild } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faNpm, faGooglePlay } from '@fortawesome/free-brands-svg-icons';
import { faPlay, faLink } from '@fortawesome/free-solid-svg-icons';
import { Col, Row, Container } from 'react-bootstrap';

interface ProjectChildren {
    links?: {
        github?: string,
        npm?: string,
        demo?: string,
        play?: string,
        more?: string,
        gplay?: string,
        customs: ReactChild
    }
    description: any,
    fullContent: ReactChild,
}

export function Option({ id, link, text, icon }: any) {
    return (link ?
        <Col xs={3} sm={3} md={4} style={{ padding: "5px 3px" }}>
            <a id={id} href={link} target="_blank" rel="noopener noreferrer" className="link">
                <FontAwesomeIcon icon={icon} /><span> {text}</span>
            </a>
        </Col> : null);
}

interface Props {
    title: string,
    children: ProjectChildren,
    previewImage?: string,
    preview?: string,
    width: number;
    date: Date;
}

interface State {
}

export class Project extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
        }
    }

    render() {
        const { links, fullContent, description } = this.props.children
        const idBase = 'op-'+this.props.title;
        return <Col md={this.props.width || 6} className="project-container" >
            <Container className="project">
                <Row>
                    <Col style={{ padding: "0px" }}>
                        <Row>
                            <Col>
                                <h2>{this.props.title}</h2><span className="date">{this.props.date?.getFullYear()}</span>
                            </Col>
                        </Row>
                        <Row>
                            {links?.github ? <Option id={idBase+'-code'} link={links.github} text="code" icon={faGithub} /> : null}
                            {links?.npm ? <Option id={idBase+'-package'} link={links.npm} text="package" icon={faNpm} /> : null}
                            {links?.demo ? <Option id={idBase+'-demo'} link={links.demo} text="demo" icon={faPlay} /> : null}
                            {links?.play ? <Option id={idBase+'-play'} link={links.play} text="play" icon={faPlay} /> : null}
                            {links?.more ? <Option id={idBase+'-more'} link={links.more} text="more" icon={faLink} /> : null}
                            {links?.gplay ? <Option id={idBase+'-gplay'} link={links.gplay} text="GooglePlay" icon={faGooglePlay} /> : null}
                            {links?.customs}
                        </Row>
                        <Row>
                            <Col>
                                {this.props.preview ? this.props.preview : this.props.previewImage ? <Row>
                                    <img className="previewImg" src={this.props.previewImage} alt="preview"></img>
                                </Row> : null}
                                <Row style={{ textAlign: "left" }}>
                                    {description}
                                </Row>
                                <Row style={{ textAlign: "left" }}>
                                    {fullContent}
                                </Row>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        </Col>;
    }

}